<template>
    <div class="main_p">
        <div class="banner">
            <!-- <img :src="require(`../img/main_page1.png`)" alt=""> -->
            <p>PASTEL <em> PALETTE</em></p>
            <div class="mc_box">
                <li class="mc_li"></li>
                <li class="mc_li"></li>
                <li class="mc_li"></li>
                <li class="mc_li"></li>
                <li class="mc_li"></li>
                <li class="mc_li"></li>
            </div>
            <!-- <img src="../img/main3.png" alt=""> -->
        </div>
        <h1>    ?</h1><p>{{c_txt}}</p>
        <div class="h_con">
        <template v-for="(color, indexs) in m_clist">
            <div class="hex_box" @click="link_c(color)" :key="indexs" v-if="color.tag.join().toLowerCase().includes(dice)">
                <router-link :to="{name: 'content1', params : {dice_color : color.hex }}"></router-link>
                <div class="hex_card">
                    <img :src="require(`../img/${color.hex}.png`)" alt="">
                    <p>{{ color.hex }}</p>
                </div>
            </div>
        </template>
        </div>
    </div>
</template>
<script>
import $ from 'jquery';
import list from './list.js'
export default {
    // name : 'main',
    data() {
        return {
            m_clist : list,
            dice : "",
            c_txt : "",
        }
    },
    mounted() {
        // console.log("");
        const random = Math.floor(Math.random() * 7); //0~6
        if(random == 0) {
            this.dice = 'red'
            this.c_txt = "        ."
        }else if(random == 1) {
            this.dice = 'blue'
            this.c_txt = '       .        ,     .      .'
        }else if(random == 2) {
            this.dice = 'purple',
            this.c_txt = '      .'
        }else if(random == 3) {
            this.dice = 'brown'
            this.c_txt = '      .'
        }else if(random == 4) {
            this.dice = 'green'
            this.c_txt = ' ,     ,    '
        }else if(random == 5) {
            this.dice = 'beige'
            this.c_txt = '     .'
        }else if(random == 6) {
            this.dice = 'yellow'
            this.c_txt = ' ,       ,  ,    .    !'
        }
        var k = []; //  
        var mc_box = document.querySelectorAll('.mc_li');
        for (var ss = 0; ss < 6; ss++) {
            var ii = Math.floor(Math.random() * this.m_clist.length);
            if (k.indexOf(ii) === -1) { // k ii  -1 .   
                k.push(ii) //k ii   
                $(mc_box[ss]).css({
                    'backgroundColor' : this.m_clist[k[ss]].hex
                })
            }else{ //     
                ss--
            }
        }
        
        

    },
    methods: {
        link_c(color) {
            this.$router.push({name: 'content1', params : {dice_color : color.hex.toLowerCase()}})
        },
            
    }
}
</script>
